@use "../abstracts/variables";

.hero {
  &--simple {
    position: relative;

    & > .hero__image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .hero__description {
      position: relative;
      padding-top: 150px;
      padding-bottom: 150px;
    }
  }

  &--slider {
    .swiper {
      &-pagination {
        position: absolute;
        z-index: 1;
        bottom: 40px;
        display: flex;
        width: 100%;
        justify-content: center;

        &-bullet {
          width: 10px;
          height: 10px;
          border: 1px solid variables.$color-black;
          background-color: variables.$color-white;
          border-radius: 50%;

          &-active {
            background-color: variables.$color-black;
          }

          &:not(:last-child) {
            margin-right: 10px;
          }
        }
      }
    }
  }
}
