%flex-center {
  display: flex;
  align-items: center;
}

%reset-buttons {
  padding: 0;
  border-style: none;
  margin: 0;
  background-color: transparent;
}

.header {
  z-index: 2;
  padding: 0 50px;
  border-bottom: 1px solid #c1c1c1;
  background-color: #FFF;
  --columns: 2fr 7fr 2fr;
  --column-gap: 15px;

  a {
    color: #000;
    text-decoration: none
  }

  &__logo {
    @extend %flex-center;
    position: relative;
    z-index: 1;

    a {
      display: block;
      font-weight: 600;
      letter-spacing: 3px;
      text-decoration: none;
      text-transform: uppercase;
    }
  }

  &__user {
    position: relative;
    z-index: 1;

    ul {
      height: 100%;
      list-style: none;
      --justify: flex-end;
      --gap-flex: 15px;

      li {
        a {
          display: block;
        }

        svg {
          width: 20px;
          height: 20px;
          color: black;
          
          &.icon-cart {
            width: 40px;
            height: 40px;
          }
        }

        button {
          @extend %reset-buttons
        }
      }
    }
  }

  @media screen and (max-width: 800px) {
    padding: 20px;
    --columns: auto 6fr 2fr;
  }
}

.nav {
  display: flex;
  justify-content: center;

  .burger-nav {
    display: none;
    @extend %reset-buttons;

    svg {
      width: 20px;

      &.icon-close {
        display: none;
      }
    }
  }

  &__child {
    display: flex;
    height: auto;
    --justify: center;
    --gap-flex: 30px;
    --align: stretch;
    flex-wrap: wrap;
    --gap-flex: 30px;

    &-item {
      display: flex;
      padding: 20px 0;
      border-bottom: 2px solid transparent;
      text-transform: uppercase;

      &:hover {
        border-bottom: 2px solid #000;
      }

      > * {
        margin: auto 0;
      }
    }
    
    &-button-dropdown {
      @extend %reset-buttons;
      @extend %flex-center;
      text-transform: inherit;

      svg {
        height: 5px;
        color: black;
        transition: transform ease .2s;
        padding: 3px;
      }

      &[data-active="true"] {
        & ~ .child__level-dropdown {
          display: grid;
          grid-column-gap: var(--column-gap,10px);
          grid-row-gap: var(--row-gap,10px);
          column-gap: var(--column-gap,10px);
          grid-template-columns: var(--columns,repeat(3,1fr));
          grid-template-rows: var(--rows,1fr);
          row-gap: var(--row-gap,10px);
        }
  
        svg {
          transform: rotateZ(180deg);
        }
      }
    }
    
    .child__level-dropdown {
      position: absolute;
      top: 100%;
      left: 0;
      display: none;
      width: calc(100% - 100px);
      padding: 50px 50px 50px;
      background-color: #FFF;
      box-shadow: 0px 15px 10px rgba(0, 0, 0, 0.20);
      
      --columns: 1fr 1fr 1fr 1fr;
    }

    .child__level {
      &-first {
        display: flex;
        flex-direction: column;
        > a {
          margin-bottom: 10px;
          display: block;
          font-weight: 600;
          text-transform: uppercase;
        }
      }
      
      &-second {
        text-transform: initial;

        &:last-of-type {
          margin-bottom: 25px;
        }
      }
    }
  }

  @media screen and (max-width: 800px) {
    display: flex;
    align-items: center;
    order: -1;

    .burger-nav {
      display: block;
    }

    &__child {
      position: fixed;
      top: 70px;
      left: 0;
      width: 100%;
      height: calc(100% - 70px);
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      animation: slidein .3s ease;
      background-color: #fff;
      overflow-y: auto;

      &[data-active="false"] {
        display: none;
      }
      &[data-active="true"] {
        display: block;
      }

      &-item {
        display: block;
        width: calc(100% - 40px);
        height: auto;
        padding: 0 20px;
        border-bottom: 1px solid;
        margin: 0;

        & > * {
          padding: 20px 0;
        }

        & > a {
          display: block;
        }
      }

      &-button-dropdown {
        display: flex;
        width: 100%;
        justify-content: space-between;

        svg {
          height: 10px;
        }

        &[data-active="true"] {
          & ~ .child__level-dropdown {
            display: block;
          }
    
          svg {
            transform: rotateZ(180deg);
          }
        }
      }

      .child__level-dropdown {
        position: static;
        width: 100%;
        padding: 0 0 20px;
        box-shadow: none;
        display: none;
      }
      

      @keyframes slidein {
        from {
          transform: translateX(-100%);
        }
      
        to {
          transform: translateX(0%);
        }
      }
    }

  }
}
